body {
  font-family: "HelveticaNeue-Light", "Helvetica Neue", "Helvetica", Arial, "Hiragino Sans GB", "Hiragino Sans GB W3", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
  clear: both;
}
ul,
ol {
  list-style: none;
}
[hidden] {
  display: none !important;
}



